.contact {
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Satoshi", sans-serif;
    padding: 0 1rem;
    margin-top: 10rem;
}

.let__contact {
    margin-top: 2rem;
    display: flex;
    font-size: 10rem;
    justify-content: center;
    text-align: center;
    border-bottom: 1px solid var(--nav-overlay-color);
    align-items: center;
    transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
    position: relative;
    top: 5rem;

}

.let__contact h1 {
    color: #3c3c3c;
    letter-spacing: 1rem;

}

.contact__container {
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
    flex-direction: column;
}

.contact__title {
    text-align: left;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
    margin-left: 1rem;
}


.contact__form {
    width: 30.75rem;
}

.contact__info {
    display: flex;
    justify-content: left;
    gap: 10rem;
    font-family: "Satoshi", sans-serif;

}

.contact__details h1 {
    font-size: var(--h1-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.contact__merged {
    display: flex;
    justify-content: left;
    justify-content: space-between;
    font-family: "Satoshi", sans-serif;
    margin-bottom: 2rem;
    position: relative;

}

.contact__merged input {
    width: 100%;
    height: 3rem;
    border-radius: 0.0rem;
    border-bottom: 2px solid var(--text-color);
    outline: none;
    font-size: 1rem;
    margin: 0rem 0.5rem;
    font-family: "Satoshi-Bold", sans-serif;
    gap: 1rem;
}


.contact__socials {
    display: flex;
    margin-bottom: 21rem;
    width: 50%;
}

.contact__button {

    color: var(--title-color);
    font-size: var(--h2-font-size);
    display: inline-block;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
    transition: 0.6s;
    text-align: center;
    display: flex;
    padding-top: 2rem;
    padding-left: 1rem;
    font-family: "Satoshi-Bold", serif;
    position: relative;
}

.contact__button::after {
    content: "";
    position: absolute;
    left: 1rem;
    top: 5rem;
    width: 70%;
    height: 2px;
    background-color: var(--title-color);
    transform: scaleX(0);
    transform-origin: left;
    transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
}

.contact__button:hover::after {
    transform: scaleX(1);
}


.contact__form-div {
    position: relative;
    margin-bottom: var(--mb-2);
    height: 3rem;

}




.contact__form-input {

    width: 100%;
    height: 100%;
    background: transparent;
    color: var(--text-color);
    outline: none;
    border-radius: 0rem;
    margin: 1rem 0rem;
    padding: 0.2rem 0rem;
    z-index: 1;
    font-family: "Satoshi-Bold", sans-serif;
    font-size: var(--normal-font-size);
    letter-spacing: 0.1rem;
    margin: 0rem 0.5rem;
    transition: 0.3s ease;
}


.contact__form-div input {
    border-radius: 0rem;
    border-bottom: 2px solid var(--text-color);
}

.contact__merged select {
    padding-bottom: 1rem;
    padding-top: 0.5rem;
    border-radius: 0rem;
    border-bottom: 2px solid var(--text-color);
    background: transparent;
    color: var(--text-color);
    outline: none;
    font-size: var(--normal-font-size);
    font-family: "Satoshi-Bold", sans-serif;
    margin: 0rem 0.5rem;
    transition: 0.3s ease;

}

.contact__form-div textarea {
    resize: vertical;
    height: 100%;
    width: calc(100% + 17px);
    /* Adjust for scrollbar width */
    background: transparent;
    color: var(--text-color);
    outline: none;
    z-index: 1;
    overflow-y: scroll;
    scrollbar-width: thin;

}

.contact__form-div textarea::-webkit-scrollbar {
    width: 0.0rem;
    /* Adjust as needed */
    background-color: transparent;

}

.contact__form-div textarea::-webkit-scrollbar-thumb {
    background-color: transparent;
}


.contact__form-area {
    width: 100%;
    height: 5rem;
    background: transparent;
    color: var(--text-color);
    outline: none;
    border-radius: 0.75rem;
    z-index: 1;
    padding: 1rem 0rem;
    border-bottom: 2px solid var(--text-color);
    border-radius: 0rem;

}

.contact__form-area textarea {
    resize: none;

}

.button--flex {
    transition: 0.6s ease;
}

.contact__email h3 {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    font-family: "Satoshi-Bold", sans-serif;
    color: var(--title-color);
}

.contact__email p {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-0-5);
    font-family: "Satoshi", sans-serif;
    color: var(--text-color);
    position: relative;
}

.contact__email p::after {
    content: "";
    position: absolute;
    left: 0rem;
    top: 2rem;
    width: 70%;
    height: 1.2px;
    background-color: var(--text-color);
    transform: scaleX(0);
    transform-origin: left;
    transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
}

.contact__email p:hover::after {
    transform: scaleX(1);
}

.contact__location h3 {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    font-family: "Satoshi-Bold", sans-serif;
    color: var(--title-color);
}

.contact__location p {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-0-5);
    font-family: "Satoshi", sans-serif;
    color: var(--text-color);
}


.custom-dropdown {
    display: flex;
    justify-content: center;
    align-self: center;
    text-align: center;
    align-items: center;
    height: 100%;
    background: transparent;
    color: var(--text-color);
    position: relative;
}

.dropdown-select {
    appearance: none;
    border: none;
    background: transparent;
    padding: 1.6rem;
    font-size: 1rem;
    cursor: pointer;
}

.dropdown-icon {
    position: absolute;
    top: 45%;
    right: 1.375rem;
    transform: translateY(-50%);
    pointer-events: none;
    color: var(--title-color);
    font-size: 1.2rem;
}

.contact__lower {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding-bottom: 8rem;
}

.contact__checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    text-align: left;
    width: 70%;
}

.contact__checkbox p {
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-0-5);
    font-family: "Satoshi", sans-serif;
    color: var(--text-color);
    margin-top: 1.5rem;
}

.contact__checkbox input[type="checkbox"] {
    display: flex;
    appearance: none;
    position: relative;
    width: 1rem;
    height: 1rem;
    border: 0.09rem solid var(--text-color);
    border-radius: 0.25rem;
    margin-right: 0.5rem;
    outline: none;
    cursor: pointer;
    padding: 0.5rem;

}

.contact__checkbox input[type="checkbox"]::before {
    content: "";
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 6px;
    height: 10px;
    border-style: solid;
    border-color: var(--title-color);
    border-width: 0 2px 2px 0;
    transition: all 0.3s ease;
    opacity: 0;
}


.contact__checkbox input[type="checkbox"]:checked {
    background-color: var(--contaienr-color);
    color: var(--title-color);
    border-color: var(--title-color);


}

.contact__checkbox input[type="checkbox"]:checked::before {
    opacity: 1;
    transform: translate(-50%, -50%) rotate(45deg);
}

.contact__load-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    z-index: 10;
    pointer-events: none;
}

.contact__load-screen {
    position: relative;
    padding-top: 0px;
    padding-left: 0px;
    padding-right: 0px;
    background-color: var(--contact-transition-color);
    width: 0%;
    height: 100%;
}

