@media (max-width: 454px) {
    /* Styles for mobile phones */

    .contact {
        width: 100%;
    }


    .let__contact {
        padding-top: 2rem;
        font-size: 3rem;
        letter-spacing: normal;
        margin-bottom: 3rem;
    }

    .contact__entire {
        overflow-x: hidden;
    }

    .contact__container {
        display: flex;
        gap: 1rem;
        flex-direction: column;
        padding: 0rem 1rem;
        justify-content: center;
        align-items: center;
        height: 100%;

    }

    .contact__title {
        text-align: center;
    }

    .contact__form {
        width: 100%;
    }

    .contact__info {
        display: flex;
        justify-content: center;
        text-align: center;
        align-self: center;
        align-items: center;
        gap: 0.4rem;
    }

    .contact__location {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        align-self: center;
        align-items: center;
    }

    .contact__location h3 {
        font-size: 1rem;
    }

    .contact__location p {
        font-size: 1rem;
    }

    .contact__form-input {
        width: 100%;
        font-size: 0.9rem;
        padding: 0 0;
        margin: 0 0;
    }

    .contact__form-input input {
        border-bottom: 1px solid var(--text-color);
    }

    .contact__merged input {
        width: 100%;
        font-size: 0.9rem;
        padding: 0 0;
        margin: 0 0;
        border-bottom: 1px solid var(--text-color);
    }

    .contact__form-input:focus {
        width: 100%;
        font-size: 0.9rem;
        padding: 0;
        margin: 0;
        border-bottom: 1px solid var(--text-color);
        background-color: red;
    }

    .dropdown-select {
        width: 100%;
        font-size: 0.3rem;
        padding: 0 0rem;
        margin: 0 0;

    }

    .contact__form-div textarea {
        width: 100%;
        font-size: 0.9rem;
        padding: 0 0;
        margin: 0 0;

    }

    .contact__form-area {
        width: 100%;
        font-size: 0.9rem;
        padding: 0 0;
        margin: 0 0;
        border-bottom: 1px solid var(--text-color);
    }

    .dropdown-icon {
        font-size: 0.6rem;
        right: 0.5rem;
    }

    .dropdown-select {
        font-size: 0.6rem;
        padding: 0.6rem 1.6rem;

    }

    .contact__merged select {
        font-size: 0.9rem;
        padding: 0.6rem 1rem;
        gap: 1rem;
        margin: 0;
        border-bottom: 1px solid var(--text-color);
    }


    .contact__details,
    .contact__socials {
        text-align: center;
    }

    .contact__details h1 {
        font-size: 1.75rem;
    }

    .contact__merged {
        align-items: flex-start;
    }

    .contact__merged input {
        margin-bottom: 1rem;
    }

    .contact__button {
        width: fit-content;
        margin: 0 auto;
        padding-top: 1.5rem;
    }

    .contact__email,
    .contact__location {
        margin-bottom: 1.5rem;

    }

    .contact__location p {
        font-size: 1rem;
    }
    .contact__location h3 {
        font-size: 1.1rem;
    }

    .contact__email h3 {
        font-size: 1.3rem;
    }

    .contact__email p {
        font-size: 0.9rem;
    }

    .contact__info {
        gap: 1.5rem;
    }

    .contact__checkbox p {
        text-align: center;
        font-size: 0.79rem;
        padding-bottom: 1rem;
    }

    .contact__lower {
        flex-direction: column;
        align-items: center;
        padding-bottom: 4rem;
    }
}